import { createApp } from 'vue' ;
import { createWebHistory, createRouter } from "vue-router";
import App from './App.vue'
import { createStore } from 'vuex'
/* import NotFoundComponent from './components/NotFoundComponent.vue'; */
import ENUM from './enums.js';

/* import ProductPageModal from './components/ProductPageModal.vue'; */
import ProductPageModal from './components/ProductPageModal.vue';
import MainPage from './components/MainPage.vue';
import CartPage from './components/CartPage.vue';



const routes = [
  {
    path: '/',
    name: 'MainPage',
    component: MainPage,
    children: [
        {
          path: '/product/:product_id',
          name: 'ProductPageModal',
          component: ProductPageModal,
          props: true,
        },
    ],
  },
  {
    path: '/cart',
    name: 'CartPage',
    component: CartPage,
  },
];

const router = createRouter({
  history: createWebHistory('/app'),
  routes,
});

/* export default router; */



const store = createStore({
  state () {
    return {
      STATIC_URL: 'http://vuetest.fast-code.ru/',
      apiState: ENUM.INIT,
      cart_amount: 0,
      products: [],
      products_in_cart: {},
      product: {},
      current_product: {},
      product_attributes: {},
      popup_message_title: '',
      popup_message_show: false,
    }
  },
  mutations: {
    setApiState(state, apiState) {
      state.apiState = apiState;
    },
    setProductAttribute(state, attr) {
      var attribute = attr['attribute']
      var attribute_value = attr['value']
      var attribute_lvl = attr['lvl']
      console.log('starting to set attrs', attribute, attribute_value, attribute_lvl)
      state.product_attributes[attribute] = {
        'value': attribute_value,
        'lvl': attribute_lvl
      }
      console.log('from state: ', state.product_attributes)
    },
    stashProductAttributes(state) {
      state.product_attributes = {}
    },
    showPopupMessage(state, message) {
      state.popup_message_title = message;
      state.popup_message_show = true;
      setTimeout( () => {
        this.state.popup_message_show = false;
      }, 2000);
    },
    change_count(state, count) {
      state.count = count
    },
    setProducts(state, products) {
        products.forEach( product => state.products.push(product))
    },
    setProductsInCart(state, products_in_cart) {
        products_in_cart.forEach( product_in_cart => 
        state.products_in_cart[product_in_cart['product_id']] = product_in_cart['quantity'])
    },
    addProductToCart(state, product_id) {
      state.products_in_cart[product_id.toString()] = 1
    },
    addProductQuantity(state, product_id) {
      state.products_in_cart[product_id.toString()] += 1
    },
    deleteProductFromCart(state, product_id) {
      delete state.products_in_cart[product_id.toString()]
    },
    deleteProductQuantity(state, product_id) {
    state.products_in_cart[product_id.toString()] -= 1
    },
    setCartAmount(state, cart_amount) {
      state.cart_amount = cart_amount;
    },
    setProduct(state, product) {
      state.product = product;
    },
    setCurrentProduct(state, product) {
      console.log('call set current product', product)
      state.current_product = product;
    },
  },
  getters: {
    getProductByid: (state) => (product_id) => {
      var product = state.products.find(pr => pr['id'] == product_id);
      return product;
    },
    getCartProducts(state){
      var keys = Object.keys(state.products_in_cart);
      var cart_products = []
      state.products.forEach( product => {
        var id = product.id.toString()
        if (keys.includes(id)) {
          cart_products.push(product);
        }
      })
      return cart_products;
    }
  }
})


/* const router = createRouter({ history: createWebHistory(), routes}) */
const app = createApp(App)
app.use(store)
app.use(router)

app.mount('#app')
