export function getProductsInCart() {
    axios.get(
    '/cart/get_products_in_cart_api',
    ).then((response) => {
    var products_in_cart = response.data['products_in_cart'];
    this.$store.commit('setProductsInCart', products_in_cart)
    })
}
export function getCartAmount() {
    axios.get(
    '/cart/get_cart_amount_api',
    ).then((response) => {
    var cart_amount = response.data['cart_amount'];
    this.$store.commit('setCartAmount', cart_amount)
    })
}

export async function addToCart(product_id) {
    await axios.get(
        '/cart/add_item_ajax', {
            params: {
                product_id: product_id,
            }
        }
    ).then(() => {  
        // var status = response.data
        this.$store.commit('addProductToCart', product_id)
    })
}
export async function addProductQuantity(product_id) {
    await axios.get(
        '/cart/add_item_quantity_ajax', {
            params: {
                product_id: product_id,
            }
        }
    ).then(() => {  
        // var status = response.data
        this.$store.commit('addProductQuantity', product_id)
    })
}
export async function deleteProductQuantity(product_id) {
    await axios.get(
        '/cart/delete_item_quantity_ajax', {
            params: {
                product_id: product_id,
            }
        }
    ).then((response) => {  
        var removed = response.data.removed
        /* console.log('removed: ', removed) */
        if (removed) {
            this.$store.commit('deleteProductFromCart', product_id)
        } else {
        this.$store.commit('deleteProductQuantity', product_id)
        }
    })
}
export async function deleteFromCart(product_id) {
    await axios.get(
        '/cart/delete_from_cart_ajax', {
            params: {
                product_id: product_id,
            }
        }
    ).then(() => {
        // var status = response.data
        this.$store.commit('deleteProductFromCart', product_id)
    })
}

