<template>
<div>
<div class="product__item">
    <div class="product__item__mainpart">

    <div class="product__item__image">
        <!-- <div class="product__item__sale_hint"
        v-if="product.sale_price > 0">
            <span>
                - {{ this.getSalePercent(product) }} %
            </span>
        </div> -->

        <router-link v-bind:to="'/product/'+product.id">
        <img :src="this.$store.state.STATIC_URL +product.imgsrc" alt="">
        </router-link>
    </div>

    <div class="product__item__info">
        
        <div class="product__item__name">
            <a
            href="{% url 'products:product' product.id %}">
                {{ product.name }}
            </a>
        </div>
        <div class="product__item__description"
        v-if="product.description != 'nan'">
            {{ product.description }}
        </div>
        </div>
        </div>
        <!-- endof product_item__mainpart --> 
        <div class="product__item__bottom">
            <span class="product__item__price">
                {{ this.checkProductPrice() }} ₽
            </span>
            <span class="product__item__addcart"
            value="{{ product.id }}">
                <span class="in__cart__buttonstyle noselect"
                v-if="this.isInCart(product.id)"
                @click="this.deleteFromCartClicked(product.id)"
                >
                Удалить с корзины
                </span>
                <span v-else>
                    <span class="not__incart__buttonstyle noselect"
                    @click="this.addToCartClicked(product.id)"
                    v-if="product.product_type == 'single'">
                        В корзину
                    </span>
                    <router-link v-bind:to="'/product/'+product.id"
                    class="not__incart__buttonstyle noselect nolink white"
                    v-else>
                        Выбрать
                    </router-link>
                </span>
            </span>
        </div>

</div>

<div class="product__item__mobile">
    <div class="product__item__mobile__img">
        <img :src="this.$store.state.STATIC_URL +product.imgsrc"
        alt="">    
    </div>
    <div class="product__item__mobile__info">
        <div class="product__item__mobile__info__name">
            {{ product.name }}
        </div>
        <div class="product__item__mobile__info__desc"
        v-if="product.description != 'nan'">
            {{ product.description }}
        </div>
        <div class="product__item__mobile__info__addcart">
            <div
            v-if="!this.isInCart(product.id)"
            >
                <span
                class="product__item__mobile__info__addcart__button noselect"
                v-if="product.product_type == 'single'"
                @click="this.addToCartClicked(product.id)"
                >{{ product.price }} ₽
                </span>
                <span
                v-else>
                    <router-link class="nolink noselect white"
                    v-bind:to="'/product/'+product.id">
                        <span
                        class="product__item__mobile__info__addcart__button">
                            {{ this.checkProductPrice() }} ₽
                        </span>
                    </router-link>
                </span>
            </div>
            <span
            class="not_in_cart noselect
            product__item__mobile__info__addcart__button"
            v-else
            @click="deleteFromCartClicked(product.id)"
            >
                Удалить с корзины
            </span>
        </div>
    </div>
</div>

</div>

<!-- 
<div class="product__item__price">
            <div v-if="product.sale_price > 0">
                <span class="product__item__price__sale"> 
                    {{ product.sale_price }} Р
                </span>
                <span class="product__item__price__regular">
                    {{ product.price }}
                </span>
            </div>
            <span class="product__item__price__regular2" v-else> 
                {{ product.price }} Р
            </span>

        </div> -->

</template>




<script>

import {addToCart, deleteFromCart, getCartAmount} from '../api.js';

export default {
  name: 'Products',
  props: {
      product: {},
  },
  components: {
  },
  computed: {
  },
  methods: {
      addToCart,
      deleteFromCart,
      getCartAmount,
        getProductPrice(product) {
            if (product.sale_price > 0) {
                return product.sale_price
            } else {
                return product.price
            }
        },
        checkProductPrice () {
          if (this.product.product_type == 'single') {
              return this.getProductPrice(this.product)
          } else if (this.product.product_type == 'variative') {
              if (this.product.variations) {
                return 'от ' + this.getProductPrice(this.product.variations[0])
              } else {
                  return this.getProductPrice(this.product)
              }
          }
      },
      async addToCartClicked(product_id) {
          console.log('start adding to cart')
          await this.addToCart(product_id);
          await this.getCartAmount();
      },
      async deleteFromCartClicked(product_id) {
          await this.deleteFromCart(product_id);
          await this.getCartAmount();
      },
      isInCart(product_id) {
        var in_cart = this.$store.state.products_in_cart
        var in_cart_ids = Object.keys(in_cart)
        var is_in_cart = in_cart_ids.includes(product_id.toString())
        return is_in_cart;
      },
      getSalePercent(product) {
          return parseInt((product.sale_price * 100) / product.price)
      },
  }
}
</script>

