<template>
    <div>
    <div class="product__page modal"
    v-if="this.apiStateLoaded()"> 
        <div class="product__main__container">
            <div class="product__main__container__img">
                <img :src="this.$store.state.STATIC_URL + current_product.imgsrc"
                class="product__mainimage__item">
            </div>
            <div class="product__main__container__second">
                    <div>
                    <div class="hints__block">
                        <div class="sale__block__hint"
                        v-if="current_product.sale_price > 0">
                            <span>
                                - {{ getSalePercent(current_product) }} %
                            </span>
                        </div>
                    </div>
                        <div class="product__page__name">
                            <div style="font-size: 14px;">
                                {{ this.current_product }}
                            </div>
                        <span>
                            {{ current_product.name }}
                        </span>
                        </div>
                        <div>
                            {{ current_product.size }}
                        </div>
                        <div class="product_desc"
                        v-if="current_product.description != 'nan'">
                                <div class="">
                                    {{ current_product.description }}
                                </div>
                        </div>
                        <!-- products attributes -->
                        <div>
                            {{ this.current_attributes }}
                        </div>
                        <div>
                            {{ this.product_attr }}
                        </div>
                        <div class="variants_block"
                        v-for="attr in Object.keys(this.current_attributes)"
                        v-bind:key="attr"
                        >
                            <AttributeItem
                            v-for="attr_item in this.current_attributes[attr].values"
                            v-bind:key="attr_item"
                            v-bind:attr="attr"
                            v-bind:attr_item="attr_item">
                            </AttributeItem>
                        </div>
                        <!-- eof products attributes -->


                    <div class="product__main__container__second__priceblock"
                    v-if="current_product.sale_price > 0">
                        <span class="product__main__container__second__sale">
                            {{ current_product.sale_price }} &#8381;
                        </span>
                        <span class="product__main__container__second__old">
                            {{ current_product.price }} &#8381;
                        </span>
                    </div>
                    <span class="product__main__container__second__price"
                        v-else>
                        {{ current_product.price }} &#8381;
                    </span>
                    
                    <div class="product__economy"
                    v-if="current_product.sale_price > 0">
                        <span>
                            Экономия {{ current_product.price - current_product.sale_price }} &#8381;
                        </span>
                    </div>
                    </div>
                    <!-- cart button -->
                    <div class="addcart_container">
                        <span class="addcart_button pointer noselect"
                        id="product__addcart__button"
                        @click="this.addToCartClicked(current_product.id)">
                            Добавить в корзину
                        </span>
                    </div>
                    <!-- eof cart button -->
                </div>
        </div>
    </div>
    <div class="product_page_back"
    @click="$router.push('/')"></div>
    </div>
</template>

<script>

import {getProductByIdAPI, addToCart, getCartAmount} from '../api.js';
import ENUM from '../enums.js';
import AttributeItem from '../components/AttributeItem.vue';

export default {
  name: 'ProductPageModal',
  props: ['product_id'],
  components: {
      AttributeItem,
  },
  data () {
    return {
        current_product: {},
        current_attributes: {}
    }
  },
  beforeMount () {
      this.$store.commit('setApiState', ENUM.LOADING)
  },
  async mounted () {
      console.log('start mouting')
      await this.getProductByIdAPI(this.product_id)
      if (this.product.variations.length == 0) {
          this.current_product = this.product;
      } else {
      this.current_product = this.product.variations[0]
      }
      this.current_attributes = this.collectAttributes()
      this.$store.commit('setApiState', ENUM.LOADED)
  },
  computed: {
      product() {
          return this.$store.state.product;
      },
      apiState() {
          return this.$store.state.apiState;
      },
      product_attr() {
          return this.$store.state.product_attributes;
      }
    },
    methods: {
        getProductByIdAPI,
        addToCart,
        getCartAmount,
        calcDeactive(attr, attr_item) {
            var res = Object.fromEntries(Object.entries(this.product_attr).filter(([k]) => this.product_attr[k].lvl == 1));
            if (!Object.keys(res).length == 0) {
                if (this.current_attributes[attr].lvl > 1) {
                    var set_attr_key = Object.keys(res)[0]
                    var set_attr = res[set_attr_key]
                
                    var m = this.product.variations.filter(
                        (item) => {
                            if ( (item['attributes'][set_attr_key].value == set_attr.value) && 
                            (item['attributes'][attr].value == attr_item) ) {
                                return true;
                            } else {
                            return false;
                            }
                        }
                    );
                    if (m.length ==  0) {
                        return true
                    } 
                } else {return false;}
            }
            return false
        },
        collectAttributes() {
            var attributes = {}
            var variations = this.product.variations;
            for(var i = 0; i < variations.length; i++) {
                var attr = variations[i].attributes
                var keys = Object.keys(attr)
                for (var key in keys) {
                    if (!attributes[keys[key]]) {
                        attributes[keys[key]] = {
                            values: [],
                            lvl: attr[keys[key]].lvl
                        }
                    }
                    if (!attributes[keys[key]].values.includes(attr[keys[key]].value)) {
                        attributes[keys[key]].values.push(attr[keys[key]].value)
                    }
                }
            }
            return attributes
        },  
        SetProductVariant () {
            // console.log('attr is: ', attr, '. attr_item is: ', attr_item);
            var current_attr = Object.keys(this.product_attr)
            
            

            this.current_product = this.product.variations.filter(
                 (item)  => {
                    for (var attr in current_attr) {
                        var attr_val = current_attr[attr]
                        if (item.attributes[attr_val].value == this.product_attr[attr_val].value) {
                            continue;
                            } else {
                                return false;
                            }  
                    }
                    return true;
                }
            )[0];
        },
        // SetProductVariant2 (size) {
        //     this.current_product = this.product.variations.filter(
        //         product => product.size == size
        //     )[0]
        // },
        async addToCartClicked(product_id) {
            console.log('start adding to cart')
            await this.addToCart(product_id);
            await this.getCartAmount();
            this.$router.push('/');
            this.$store.commit('showPopupMessage', 
            "Добавлено: \n" + this.current_product.name);
        },
        getSalePercent(product) {
          return parseInt((product.sale_price * 100) / product.price);
        },
        isInCart(product_id) {
            console.log('check if in cart', product_id)
            var in_cart = this.$store.state.products_in_cart
            var in_cart_ids = Object.keys(in_cart)
            var is_in_cart = in_cart_ids.includes(product_id.toString())
            return is_in_cart;
        },
        apiStateLoaded() {
            return this.apiState === ENUM.LOADED;
        },
        apiStateLoading() {
            return this.apiState === ENUM.LOADING || this.apiState === ENUM.INIT;
        },
    },
}
</script>

