<template>
    <span class="variant_item noselect pointer"
    v-bind:class="[
    (this.$parent.current_product.attributes[attr].value == attr_item)?'active':'',
    this.deactive ? 'deactive': ''
    ]"
    @click="this.setProductAttribute(attr, attr_item)"
    >
        {{ attr_item }}
    </span>
</template>

<script>
export default {
    props: {
        'attr': {},
        'attr_item': {},
    },
    data () {
        return {
            deactive: false,
        }
    },
    updated () {
        this.deactive = this.$parent.calcDeactive(this.attr, this.attr_item)
        console.log('deactive is', this.deactive);
    },
    methods: {
        setProductAttribute(attr, value) {
            if (this.deactive) {
                return console.log('cant make this field, no product with this variant')
            }
            console.clear()
            console.log('this is', this);
            console.log('this class is', this.class);
            if (this.$parent.current_attributes[attr].lvl == 1) {
                this.$store.commit('stashProductAttributes')
            }
            // console.log('attr is: ', attr, 'attr value: ', value);
            var attr_to_set = {
                'attribute': attr,
                'value': value,
                'lvl': this.$parent.current_attributes[attr].lvl,
            };
            this.$store.commit('setProductAttribute',
            attr_to_set);
            this.$parent.SetProductVariant()
        },
    }
}
</script>