<template>

    <div class="products__list__wrapper">
    <ProductCard
    v-for="product in products"
    v-bind:key="product.id"
    v-bind:product="product"
    >
    </ProductCard>
    {{ count }}
    </div>
</template>

<script>
import ProductCard from '../components/ProductCard.vue';

export default {
  name: 'Products',
  components: {
      ProductCard
  },
  mounted () {
      console.log('mount products page')
  },
  computed: {
    products () {
        return this.$store.state.products.filter(
            product => product.parent_id == null
        )
    },
    products_in_cart () {
        return this.$store.state.products_in_cart;
    },
    count () {
        return this.$store.state.count;
    }
  },
}
</script>

