<script>
import CheckOutItem from '../components/CheckOutItem.vue';
import {addToCart, addProductQuantity, deleteFromCart, deleteProductQuantity, getCartAmount} from '../api.js';

export default {
  name: 'CartPage',
  components: {
      CheckOutItem
  },
  async mounted () {
      console.log('start mouting cart page')
  },
  computed: {
        cart_products () {
            return this.$store.getters.getCartProducts;
        },
        products_in_cart () {
            return this.$store.state.products_in_cart;
        },
        cart_quantity () {
            return Object.keys(this.$store.state.products_in_cart).length;
        },
        cart_amount() {
            return this.$store.state.cart_amount;
        },
    },
    methods: {
        addToCart,
        addProductQuantity,
        deleteFromCart,
        getCartAmount,
        deleteProductQuantity,
        async addProductQuantityClicked(product_id) {
            await this.addProductQuantity(product_id);
            await this.getCartAmount();
        },
        async deleteProductQuantityClicked(product_id) {
            await this.deleteProductQuantity(product_id);
            await this.getCartAmount();
        },
        getSalePercent(product) {
          return parseInt((product.sale_price * 100) / product.price);
        },
        isInCart(product_id) {
            console.log('product id is here', product_id)
            var in_cart = this.$store.state.products_in_cart
            var in_cart_ids = Object.keys(in_cart)
            var is_in_cart = in_cart_ids.includes(product_id.toString())
            return is_in_cart;
        },
    },
}
</script>

